let id = 0
import store from '@/store'
//  Vue  diff  ID
export default function generateID() {
  return id++
}
// id0
export function restoreId() {
  const canvasState = store.state.componentData
  let max = -1
  canvasState.forEach(item => {
    if (item.id > max) max = item.id
  })
  console.log(max)
  id = max + 1
}
